.ColorPicker .circle {
  width: 30px;
  height: 30px;
  display: inline-block;
  cursor: pointer;
}

.ColorPicker .panel-title {
  font-weight: bold;
  text-transform: uppercase;
  color: black;
}

.ColorPicker .circle.isActive {
  border: 3px solid black;
  opacity: 0.7;
}
.ColorPicker .red {
  background-color: red;
}

.ColorPicker .green {
  background-color: green;
}

.ColorPicker .blue {
  background-color: blue;
}

.ColorPicker .gray {
  background-color: gray;
}

.SizeSetting .panel-body .mg-right {
  margin-right: 10px;
}

.SizeSetting .panel-title {
  text-transform: uppercase;
  font-weight: bold;
}

.SizeSetting {
  margin-bottom: 30px;
}

.Result .panel-default {
  border: 2px solid red;
}


.Result .panel-body.df {
  text-align: justify;
}


.Reset {
  margin-bottom: 30px;
}